:root {
  --primary: #28d805;
  --dark: #121013;
  --light: #f2f2f2;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--dark);
  color: var(--primary);
}

input,
textarea,
button {
  padding: 12px;
  margin: 12px 0;
  font-family: 'Inter', sans-serif;
  background-color: transparent;
  color: var(--light);
  border: 1px solid var(--primary);
  border-radius: 4px;
}

input::placeholder,
textarea::placeholder {
  color: var(--light);
  font-weight: bold;
}
.input[type='submit'] {
  cursor: pointer;
}
.input[type='submit']:hover {
  background-color: var(--primary);
  color: var(--dark);
  font-weight: 700;
}
.note-app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid 2px var(--primary);
  padding: 1rem 2rem;
}

.note-app__header h1 {
  margin: 8px 0;
  flex: 1;
  color: var(--primary);
}

.note-app__body {
  max-width: 1000px;
  margin: 16px auto;
  padding: 12px;
}

.note-app__body h2 {
  font-weight: normal;
  margin: 16px 0;
  border-bottom: solid 2px var(--primary);
}

.note-input {
  margin: 0 auto;
  max-width: 500px;
  margin-bottom: 48px;
}

.note-input input,
.note-input textarea {
  display: block;
  width: 100%;
}

.note-input__title {
  font-weight: bold;
}

.note-input__title__char-limit {
  font-size: 14px;
  text-align: right;
  color: #aaa;
}

.note-input__body {
  min-height: 175px;
}

.note-input button {
  display: block;
  width: 100%;
  padding: 8px;
  background-color: transparent;
  font-family: 'Inter', sans-serif;
  color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}

.notes-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: minmax(min-content, max-content);
  gap: 16px;
  margin-bottom: 48px;
}

.notes-list__empty-message {
  text-align: center;
  color: #aaa;
}

.note-item {
  border: 1px solid var(--primary);
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.note-item__content {
  padding: 12px;
  flex: 1;
}

.note-item__title {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-bottom: 4px;
}

.note-item__date {
  font-size: 12px;
  margin-bottom: 8px;
  color: #aaa;
}

.note-item__body {
  font-size: 14px;
}

.note-item__action {
  display: flex;
}

.note-item__delete-button,
.note-item__archive-button {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 0;
  border-top: 1px solid var(--primary);
  padding: 8px 0;
  font-family: 'Inter', sans-serif;
  font-weight: bold;
  cursor: pointer;
}

.note-item__delete-button {
  border-right: 1px solid var(--primary);
  color: #ff5f52;
}

.note-item__archive-button {
  color: #49ff00;
}

.note-item__archive-button:hover {
  color: var(--dark);
  background-color: #0be23a;
}

.note-item__delete-button:hover {
  color: var(--dark);
  background-color: #fd5d5d;
}

.note-search {
  max-width: 500px;
  margin: 0 auto;
}

.note-search__form {
  border: 1px solid var(--primary);
  border-radius: 4px;
}

.note-search form input {
  border: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-right: 50px;
  width: calc(100% - 58px);
}

.note-search form .submit {
  border: none;
  margin-right: -50px;
  width: 50px;
}

@media only screen and (min-width: 500px) {
  .notes-list {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (min-width: 800px) {
  .notes-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media only screen and (min-width: 800px) {
  .notes-list {
    grid-template-columns: repeat(4, 1fr);
  }
}
